@use 'styles/sass' as *;

.daily-header.daily-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: get-unit(2);
}

.invisible-secret-button {
  cursor: auto;
  background: none;
  border: none;

  &:hover {
    scale: 1;
  }
}

.daily-heading.daily-heading {
  color: get-color('white') !important;
  font-size: 1.5em;
  margin: 0;
  padding: 0;
}

.menu {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  width: 100%;
  font-size: 1.25rem;
  gap: get-unit(2);
  place-items: center;
  background: get-color('blue', 7);
  @include box-shadow;
  color: get-color('white');
}

.menu-hearts {
  color: get-color('red');
  font-size: 1.5rem;
}

.slick-dots {
  transform: translateY(get-unit(6));
}

.slick-slide {
  div {
    display: grid;
    place-items: center;
  }
}

.list {
  margin-left: get-unit(6);
  list-style: disc;
}

.result-answer .ant-typography {
  font-size: 1.25rem;
  font-weight: bold;
  color: get-color('blue', 6);
}

.result-answer__text .ant-typography {
  margin: get-unit(0 1);

  span {
    text-decoration: underline;
  }
}

.results-container {
  width: 100%;
  margin: get-unit(6 0 15 0);
  justify-content: center;
  gap: get-unit(4);
}

.daily-loading {
  display: grid;
  place-items: center;
  min-height: 50svh;
}

.hub {
  display: grid;
  place-items: center;
  padding: get-unit(10);
}

.hub-list {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);
  gap: get-unit(15);
}

.hub-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: get-unit(2);
  color: get-color('black');
  font-weight: bold;
}

.hub-item-disabled {
  opacity: 0.5;
}

.daily-keyboard {
  width: 100%;
  max-width: calc(100vw - get-unit(4));
  position: sticky;
  bottom: get-unit(1);
  height: var(--keyboard-height);
  margin: get-unit(0 2);
  user-select: none;

  &__row {
    display: flex;
    width: 100%;
    margin: get-unit(0 auto 2);
    touch-action: manipulation;
  }

  &__key {
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    font-size: 1.25em;
    font-weight: bold;
    border: 0;
    padding: 0;
    margin: get-unit(0 2 0 0);
    height: get-unit(16);
    min-width: 3ch;
    border-radius: get-unit(1);
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: get-color('blue', 7);
    color: get-color('white');
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
    flex: 1;

    &--backspace {
      flex: 3;
    }

    &--enter {
      flex: 5;
      font-size: 0.75em;
    }

    &:disabled,
    &--disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    &--correct {
      background-color: get-color('green', 4);
    }

    &--used {
      background-color: get-color('gray', 6);
    }

    &--incorrect {
      background-color: get-color('red', 7);
    }

    &--intermediate {
      background-color: get-color('yellow', 5);
    }
  }
}

.daily-keyboard-key {
  background: get-color('blue', 7);
  color: get-color('white');
  border-radius: $border-radius;
  border: 1px outset get-color('blue', 3);
  text-transform: uppercase;

  &:disabled {
    opacity: 0.5;
  }

  &--guessed {
    background: get-color('gray', 6);
    color: get-color('gray', 4);
  }

  &--correct {
    background: get-color('green', 5);
    color: get-color('white');
  }
}

.text-region {
  margin: get-unit(2 auto);
  width: calc(100% - get-unit(4));
  max-width: 720px;
  display: flex;
  justify-content: center;
}

.played-wrapper {
  position: relative;

  &__played {
    position: absolute;
    top: get-unit(-1);
    right: get-unit(-2);
  }
}
